@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap");
:root {
  --font-body: "Open Sans", Arial, sans-serif;
}

:root {
  --dark-green: rgb(19, 105, 60);
  --green: rgb(67, 149, 57);
  --light-green: rgb(220, 229, 224);
  --blue: rgb(17, 110, 247);
  --red: rgb(255, 0, 0);
  --black: rgb(0, 0, 0);
  --neutral-600: rgb(164, 164, 164);
  --neutral-200: rgb(241, 241, 241);
  --white: rgb(255, 255, 255);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.display-helper {
  display: none;
}

:root {
  font-family: var(--font-body), sans-serif;
  font-size: 1rem;
  font-weight: 300;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  font-size: 1.25rem;
  font-weight: 700;
}

h2 {
  font-size: 1.125rem;
  font-weight: 400;
}

input {
  font-size: 1.125rem;
  font-weight: 300;
}

.container {
  padding: 0 1.5rem;
  max-width: 43.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

nav {
  display: flex;
  padding: 1em 0;
  justify-content: center;
  margin: 1.5rem auto;
}
nav .divider {
  width: 2px;
  background-color: var(--green);
  margin: 0 0.75rem;
}

.button {
  font-weight: 700;
  letter-spacing: 1px;
  width: fit-content;
  text-transform: uppercase;
  cursor: pointer;
}
.button__add {
  background-color: var(--green);
  padding: 0.625rem 1.5rem;
  border-radius: 100px;
  color: var(--white);
  margin-left: auto;
}
.button__add:hover {
  background-color: var(--dark-green);
}

.button-group {
  display: flex;
  justify-content: center;
  margin: 1em 0;
}
.button-group .button {
  padding: 1rem 2rem;
  border-radius: 0.5rem;
}
.button-group .button__clear {
  margin-right: 1em;
  color: var(--white);
  background-color: var(--green);
}
.button-group .button__clear:hover {
  background-color: var(--dark-green);
}
.button-group .button__delete:hover {
  color: var(--red);
}

.search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.125rem;
}
.search-bar :focus {
  outline: none;
}
.search-bar__item, .search-bar__category {
  padding: 1.125rem;
  border: solid 1px var(--neutral-600) outset;
}
.search-bar__item {
  border-radius: 100px 0 0 100px;
}
.search-bar__category {
  border-radius: 0 100px 100px 0;
}

#shopping-list {
  display: flex;
  flex-direction: column;
  background-color: var(--neutral-200);
  padding: 1.875rem;
  border-radius: 1.25rem;
  margin: 1.5rem auto;
}
#shopping-list :last-child {
  margin-bottom: 0;
}

.category-group {
  margin-bottom: 1.75rem;
}
.category-group .item {
  display: flex;
  align-items: center;
  margin-bottom: 1em;
}
.category-group .item h2 {
  margin: 0 0.5em;
  margin-right: auto;
}
.category-group .item .checkmark-group {
  position: relative;
  height: 24px;
  width: 24px;
}
.category-group .item .checkmark-group .checkmark-icon {
  position: absolute;
  height: 100%;
  width: 100%;
}
.category-group .item .checkmark-group .circle {
  height: 100%;
  width: 100%;
  border: solid 2px var(--dark-green);
  border-radius: 100px;
  position: absolute;
}
.category-group .item-active {
  text-decoration: line-through;
  color: var(--neutral-600);
}
.category-group .item-active .checkmark-icon {
  display: block;
}
.category-group .item-active .circle {
  display: none;
}

.category-group h2,
.checkmark-group:hover {
  cursor: pointer;
}

.icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3px;
}
.icon-wrapper .close-icon {
  height: 12px;
  width: 12px;
}
.icon-wrapper:hover {
  background-color: var(--white);
  border-radius: 0.25em;
  cursor: pointer;
}

.divider-horizontal {
  background-color: var(--light-green);
  height: 2px;
  margin-bottom: 0.625rem;
}